import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DaoService } from 'src/app/services/dao.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { environment } from '../../../../../environments/environment'
import { NotificationService } from 'src/app/services/notification.service'

let currentProducts = [];

@Component({
  selector: 'store-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class DetailsComponent implements OnInit {

  public guid: any;
  public storeData: any;
  public env = environment
  isLoading = true

  public featuredProducts : Array<any>

  options =[
    { name: "1", value: 1},
    { name: "2", value: 2},
    { name: "3", value: 3},
    { name: "4", value: 4},
    { name: "5", value: 5},
    { name: "6", value: 6},
    { name: "7", value: 7},
    { name: "8", value: 8},
    { name: "9", value: 9},
  ]

  constructor(private route: ActivatedRoute, private daoService: DaoService, private _fb: FormBuilder, private notifyService : NotificationService) {
    this.guid = this.route.snapshot.params.guid;
  }


 
  ngOnInit(): void {
    this.isLoading = true
    this.retrieveStoreDetails(this.guid);
    this.retrievePromotionProducts();

    
  }

  retrieveStoreDetails(guid: any): void {
    this.daoService.getStorePromotionsByStoreID(guid).subscribe((data) => {
      console.log("------ this is teh store detail -----")
      if (data.success) {
        this.storeData = data['data']        
      }
    }, (error) => {
      console.log(error);
    });
  }

  retrievePromotionProducts() : void {
    this.daoService.getPromotionProductsByID(this.guid).subscribe((data) => {
      console.log("----- this is the promotion products -----")
      let result = JSON.parse(data)
      this.featuredProducts = result[0]
      if(this.featuredProducts.length > 0) {
        this.featuredProducts.map((d) => {
          d['count'] = 1;
        })
      }
      console.log(this.featuredProducts)
      this.isLoading = false
    }, (error) => {
      console.log(error)
      this.isLoading = false
    })
  }

  // change count of product 
  selectCount(count: number, product_id: string) {
    this.featuredProducts.find(item => item.products_id == product_id)['count'] = count
  }

  // add to my bag
  addToBag(product: any) {
    this.notifyService.showSuccess("The product was added to your shoppinglist.", "SeeMeSave.com")
    let data = localStorage.getItem('my_bag')
    let mybag = []
    if (data != null && data !='' && data !='null') {
      mybag = JSON.parse(data)
    }
    let is_exist = false
    for(let i=0; i<mybag.length; i++) {
      if (mybag[i].products_id == product.products_id) {
        mybag[i].count = mybag[i].count + product.count
        is_exist = true
        break
      }
    }
    if (!is_exist) mybag.push(product)

    localStorage.setItem('my_bag', JSON.stringify(mybag))
  }


  showToasterSuccess(){
    this.notifyService.showSuccess("The product was added to your shoppinglist !!", "SeeMeSave.com")
  }

  showToasterError(){
      this.notifyService.showError("Something is wrong", "ItSolutionStuff.com")
  }

  showToasterInfo(){
      this.notifyService.showInfo("This is info", "ItSolutionStuff.com")
  }

  showToasterWarning(){
      this.notifyService.showWarning("This is warning", "ItSolutionStuff.com")
  }
}
